			var index = 3 * i;

			// per-instance position offset
			offsets[ index ] = positions[ i ].x;
			offsets[ index + 1 ] = positions[ i ].y;
			offsets[ index + 2 ] = positions[ i ].z;

			object.position.x = positions[ i ].x;
			object.position.y = positions[ i ].y;
			object.position.z = positions[ i ].z;

			// per-instance scale variation
			scales[ i ] = 2 + 0.5 * Math.sin( frequency * Math.PI * i / INSTANCES );
			object.scale.x = scales[ i ];
