	applyInstancingToMeshExtended = (mesh,n) => {
		var indecis = new Float32Array( INSTANCES ); 

		for ( var i = 0, l = INSTANCES; i < l; i ++ ) {
			indecis[i] = i / INSTANCES
		}

		mesh.geometry.addAttribute('aIndex', new THREE.InstancedBufferAttribute( indecis, 1 ) )

		var uTime = { value: 0, type: 'float', stage: 'vertex' }
		mesh.material.userData.uTime = uTime
		// console.log("mesh.material.shaderUniforms.uTime",mesh.material.shaderUniforms );
		//we expose it as a primitive
		Object.defineProperty(mesh.material, 'time', {
			get:()=>uTime.value,
			set:v=>uTime.value = v
		})
		var uScale = { value: 1.5, type: 'float', stage: 'vertex' }
		mesh.material.userData.uScale = uScale
		// console.log("mesh.material.shaderUniforms.uTime",mesh.material.shaderUniforms );
		//we expose it as a primitive
		Object.defineProperty(mesh.material, 'scale', {
			get:()=>uScale.value,
			set:v=>uScale.value = v
		})

		var uSelected = { value: false, type: 'boolean' }
		Object.defineProperty(mesh, 'selected', {
			get:()=>uSelected.value,
			set:v=>uSelected.value = v
		})

		decorateMaterialWithSimpleInstancing2(mesh.material)
		mesh.customDepthMaterial = decorateMaterialWithSimpleInstancing(
			new THREE.MeshDepthMaterial({ depthPacking: THREE.RGBADepthPacking })
		)
		mesh.material.needsUpdate = true;

		mesh.geometry = new THREE.InstancedBufferGeometry().copy(mesh.geometry)

		mesh.geometry.instanceCount = INSTANCES

		// mesh.geometry.setAttribute( 'instanceColor', this.colorAttArr[n-1] );
		mesh.geometry.setAttribute( 'instanceOffset', this.offsetAttArr[n-1]);
		mesh.geometry.setAttribute( 'instanceScale', this.scaleAttArr[n-1] );
		
		// for shadow
		mesh.customDepthMaterial.userData = { uTime: mesh.material.userData.uTime }//wire this by reference, whenever prop updates material uniform this material updates too by referencing the same uniform
		// mesh.customDepthMaterial.userData = { uScale: mesh.material.userData.uScale }
	}

