  handleZIndex = () =>{
  	// console.log("hey for test");
  	if (this.state.formZIndex < 35000){
  		this.setState({
	      formZIndex: 40000
	    });
  	}else{
  		this.setState({
	      formZIndex: 30000
	    });
  	}
  	
  }

showTable = () => {
  	const width = document.documentElement.clientWidth;
    	const height = document.documentElement.clientHeight;
  	if (firstTime){
  		this.interval = setInterval(() => this.updateArrow(), 300);
  		
  		 if(isMobile){
	    	if ((width/height)<1){
	    		if (window.innerWidth < 420) {
		    		this.setState({
				      	showTableRow:"none",
		    			seTop:"90px"
				    });
		    	
	    	}
	    }
	    	if ((width/height)>1){
				this.setState({
			      	showTableRow:"none",
	    			seTop:"90px",
	    			showDesc:"none",
	    			tableFontSize:"9px",
	    			marqueeFontSize:"32px"
			    });
	    	}
	    }
	    firstTime = false;
  	}
  	if (tableShow){
  		this.setState({
	      tableDisplay: "none",
	      petitDisplay:"none"
	    });
	    tableShow = !tableShow;
	    clearInterval(this.interval);
	    firstTime = true;
	    arrowSymbols = "";
  	}else{
  		// if(!creditShow){
  			if (this.state.marqueeTxt == "ϕth Daddy Master Class ⟿ Try"){
  				var tempCreditShow = this.state.creditDisplay;
  				if (creditShow){
  					tempCreditShow = "none";
  					creditShow = false;
  				}
  				if(isMobile){
			    	if ((width/height)<1){
				    		this.setState({
						      	showDesc:"inherit"
						    });
			    	}
			    }
	  			this.setState({
			      	tableDisplay: "inherit",
		      		petitDisplay:"none",
		      		creditDisplay:tempCreditShow
			    });
			    tableShow = !tableShow;
	  		}
  		// }
  		
  	}
  }
  showCredit = () => {
  	// console.log("jey");
  	if (creditShow){
  		this.setState({
	      creditDisplay: "none"
	    });
	    creditShow = !creditShow;
  	}else{
  		if(!tableShow){
  			this.setState({
			    creditDisplay: "inherit"
		    });
		    creditShow = !creditShow;
  		}
  		
  	}
  }
  showContents = () => {
  	if (contentsShow){
  		this.setState({
	      diagonalDisplay: ""
	    });
	    contentsShow = !contentsShow;
  	}else{
  		if(!contentsShow){
  			this.setState({
			    diagonalDisplay: "diagonalAni"
		    });
		    contentsShow = !contentsShow;
  		}
  		
  	}
  }
  showContents2 = () => {
  	if (contentsShow2){
  		this.setState({
	      diagonalDisplay2: ""
	    });
	    contentsShow2 = !contentsShow2;
  	}else{
  		if(!contentsShow2){
  			this.setState({
			    diagonalDisplay2: "diagonalAni2"
		    });
		    contentsShow2 = !contentsShow2;
  		}
  		
  	}
  }

  toggleMarqueeHover= () =>{
  	// console.log("marqueeHovered",marqueeHovered);
  	if (marqueeHovered){
  		this.setState({
	      marqueeHover: ""
	    });
	    marqueeHovered = !marqueeHovered;
  	}else{
  		if (this.state.display != "display"){
  			this.setState({
		      marqueeHover: "marqueeHover"
		    });
		    marqueeHovered = !marqueeHovered;
  		}
  	}
  	
  }

  toggleMarqueeHover2= () =>{
  	// console.log("marqueeHovered",marqueeHovered);
  	if (marqueeHovered2){
  		this.setState({
	      marqueeHover2: ""
	    });
	    marqueeHovered2 = !marqueeHovered2;
  	}else{
  		if (this.state.display != "display"){
  			this.setState({
		      marqueeHover2: "marqueeHover"
		    });
		    marqueeHovered2 = !marqueeHovered2;
  		}
  	}
  	
  }

  handleWindowResize = () => {
    // const width = this.el.clientWidth;
    // const height = this.el.clientHeight;
	const width = document.documentElement.clientWidth;
    const height = document.documentElement.clientHeight;
    this.setState({
      wWidth:document.documentElement.clientWidth,
      wHeight:document.documentElement.clientHeight
    });
    if (isMobile){
    	INSTANCES = 64;
    	gltfScale = 1000;
    }else if(isBrowser){
    	INSTANCES = 128;
    	gltfScale = 1500;
    }


    // this.sceneSetup();
    var elmnt = document.getElementById("marquee");
    if (elmnt!= undefined){
    	var tableHeight = height - (elmnt.offsetHeight*2) - 6;
    var tableTop = elmnt.offsetHeight + 6;
    var showDesc = "inherit";
    var tableFontSize = "";
    var marqueeFontSize = "";
    if (isMobile){
    	if ((width / height) > 1){
	    	showDesc = "none";
	    	tableFontSize = "9px";
	    	marqueeFontSize = "32px";
	    	// tableHeight = height - (35*2) - 6;
	    }
    }
    
    this.setState({
	      tableHeight: tableHeight + "px",
	      tableTop: tableTop +"px",
	      showDesc: showDesc,
	      tableFontSize: tableFontSize,
	      marqueeFontSize:marqueeFontSize
	    });
    }

        this.camera.aspect = width / height;
    // if(isBrowser){
    	this.renderer.setSize(width, height);
    	this.camera.updateProjectionMatrix();
    	
    // }
  };

